* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  position: relative;
}
span {
  display: block;
}
.country-header {
  display: flex;
  font-size: 30px;
  font-weight: 700;
}
.country-header span{
  margin-left: 10px;
}
.country-list {
  position: absolute;
  margin: 0;
  list-style: none;
  padding: 5px;

}

.country__item {
  margin-bottom: 5px;
}
.country__item:hover {
  background-color: red;
}
.flag-pic{
  width: 30px;
}